<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartCP"
    :style="{width:canvasStyle.width,height:canvasStyle.height}"
  ></div>
</template>

<script>
import echarts from "echarts";

export default {
  name: "CustomizedPie",
  data() {
    return {
      myChart: null
    };
  },
  props: {
    animateFlag: Boolean,
    params: {
      type: Object,
      default: function() {
        return {
          canvasStyle: {
            width: "500px",
            height: "500px"
          },
          options: {
            // tooltip: {},
            // titleShow: true,
            // show: true,
            colors: [], //item的颜色
            radius: '50%',
            labelColor: '#fff',
            labelFontSize: 12,
            unti: '件',
            labelLineHeight: 20,
            itemStyle: {}
          },
          data: [
            {
              name: "A",
              value: 10
            },
            {
              name: "B",
              value: 30
            },
            {
              name: "C",
              value: 70
            }
          ]
        };
      }
    }
  },
  mounted() {
    this.initChart();
    this.$once("hook:beforeDestroy", () => {
      if (this.interval) {
        clearInterval(this.interval);
      }
      this.myChart && this.myChart.dispose();
    });
  },
  methods: {
    initChart() {
      if (this.myChart) {
        this.myChart.dispose();
      }
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(this.$refs.chartCP);
      /**
       * 数据处理
      */

      if (this.params.data.length === 0) return;
      let options = this.params.options;
      //注意：使用sort排序会改变原数组，因此会循环处罚watch中的 params 变化。可用复制数据。
      let chartData = [...this.params.data];
      chartData.sort((a, b) => a.value - b.value)

      var option;
      option = {
        title: {
          text: options.titleText ? options.titleText : '',
          left: options.titleLeft ? options.titleLeft : "center",
          top: options.titleTop ? options.titleTop : "center",
          textStyle: {
            color: this.$STANDARD.titleColor,
            // fontSize: this.$STANDARD.h3.fontSize,
            align: "center",
            // rich: rich
          }
        },
        // 
        color: options.colors,  //环形颜色
        // color:this.$STANDARD(this.styleType).pieMoreColors,
        series: [
          {
            name: options.name ? options.name : '',
            type: "pie",
            radius: options.radius ? options.radius : "50%",
            center: options.center ? options.center : ["50%", "50%"],
            data: chartData,
            roseType: "radius",
            label: {
              position: options.position ? options.position : 'outside',
              formatter: '{b}\n {c}'+ (options.unti ? options.unti : '件'),
              color: options.labelColor ? options.labelColor : '#fff',
              fontSize: options.labelFontSize,
              align: 'left',
              lineHeight: options.labelLineHeight
            },
            labelLine: {
              normal: {
                lineStyle: {
                  color: options.labelLineColor ? options.labelLineColor : null
                },
                smooth: options.labelLineSmooth ? options.labelLineSmooth : 0,
                length: 10,
                length2: 20
              }
            },
            itemStyle: options.itemStyle,
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function(idx) {
              return Math.random() * 1000;
            }
          },
          {
            type: "pie",
            radius: options.radius ? options.radius : "50%",
            // center: options.center ? options.center : ["50%", "50%"],
            data: chartData,
            roseType: "radius",
            label: {
              position: 'inner',
              formatter: '{d}%',
              color: options.labelColor ? options.labelColor : '#fff',
              fontSize: options.labelFontSize,
            },
          }
        ]
      };
      this.myChart.setOption(option);
    }
  },
  computed: {
    canvasStyle() {
      return {
        width: this.params.canvasStyle
          ? this.params.canvasStyle.width
          : "500px",
        height: this.params.canvasStyle
          ? this.params.canvasStyle.height
          : "500px"
      };
    }
  },
  watch: {
    animateFlag: {
      handler(newVal, oldVal) {
        this.initChart();
      }
    },
    "params.data": {
      handler(newVal, oldVal) {
        this.initChart();
      },
      deep: true
    }
  }
};
</script>

<style scoped>
</style>
