<!--年度、月度切换-->
<template>
  <ul class="timeChange">
    <li
      v-for="(item, index) in arr"
      :key="index"
      @click="changeTime(item)"
      :class="curTime === item ? 'active' : ''"
    >{{item}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        arr: ["年度", "月度"],
        curTime: "年度"
      }
    },
    methods: {
      changeTime(item) {
        this.curTime = item;
        this.$emit("change", this.curTime);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .timeChange
    position absolute
    width 100px
    line-height 0.28rem
    z-index 1000
    display flex
    li
      flex 1
      cursor pointer
      color #1d81c9
      font-size 0.16rem
      text-align center
      &.active
        color #82d8ff
        font-weight 700
</style>
