<!--
  echarts折线图
  @auth yw
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from "echarts";
  import {gradientColor} from "@/utils/gradient";

  export default {
    name: "lines",
    props: {
      animateFlag: Boolean,
      maps: Object,
      params: {
        type: Object,
        dafault() {
          return {
            option: {
              legendShow: true, //图例是否显示
              legendfontSize: 14, //图例字体大小
              XaxisLineShow: false, //x轴线是否显示
              xaxisLineColor: "rgba(255,255,255,0.1)", //x轴线的颜色
              axisLabelShow: false, //x轴名是否显示
              axisLabelFontSize: 12, //x轴字体大小
              yaxisLabelFontSize: 14, //y轴字体大小
              yaxisLabelColor: "#fff",
              axisLabelColor: "#fff",
              boundaryGap: true, //坐标轴是否留白
              xAxisBr: false, //x轴标签是否换行
              YaxisLineShow: true, //y轴线是否显示
              yaxisLineColor: "rgba(255,255,255,0.1)", //y轴线的颜色
              YaxisaxisLabelShow: true, //y轴刻度是否显示
              scales: true, //y轴刻度最小值是否为0
              lineWidth: 2, //指示竖线的宽度
              Lcolor: "red", //指示竖线的颜色
              splitLineShow: true, //网格线是否显示
              axisTickShow: true, //x轴刻度是否显示,
              showK: true, //y轴刻度值是否以k为单位
              legenditemWidth: 100, //图例icon的宽度
              //Tofixed:false, //y轴刻度保留小数点
              dataZoomshow: false, //是否显示datazoom
              dataZoomstart: 0,
              dataZoomend: 100,
              legendTop: "5%", //图例高度
              showSymbol: false, //是否显示折点上的圆点
              axisCategoryName: "",
              nameLocation: "start",
              XaxisNameColor: "#fff",
              axisLabelSize: 16,
              legendItemgap: 20, //legend间距
              splitXLineShow: false
            },
            colors: []
          };
        }
      }
    },
    data() {
      return {
        colors: [
          {
            line: "rgba(209, 48, 20, 1)",
            start: "rgba(62, 73, 67, .3)",
            end: "rgba(78, 173, 254, 0)"
          },
          {
            line: "rgba(29, 209, 200, 1)",
            start: "rgba(242, 104, 210, .3)",
            end: "rgba(188, 60, 113, 0)"
          },
          {
            line: "rgba(208, 45, 222, 1)",
            start: "rgba(208, 45, 222, .3)",
            end: "rgba(101, 30, 206, 0)"
          },
          {
            line: "rgba(162, 227, 105, 1)",
            start: "rgba(162, 227, 105, .3)",
            end: "rgba(60, 234, 166, 0)"
          },

          {
            line: "rgba(240, 151, 26, 1)",
            start: "rgba(240, 151, 26, .3)",
            end: "rgba(255, 88, 88, 0)"
          }
        ]
      };
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
        }
        if (this.params.data.length === 0) return;

        let options = this.params.option;
        let propData = this.params.data;
        let map = {};
        if (this.maps) {
          map = this.maps;
        } else {
          map = this.params.map;
        }
        // 获取keys数组
        let dataKeys = Object.keys(map);

        // 获取key对应的中文数组
        let legendData = Object.values(map);

        let xAxis = [];
        // 所有line数据数组
        let lineDataArr = [];
        // echarts series数据
        let seriesArr = [];
        // 根据legend设置每条line数据对应的key值
        let maxArr = [];
        dataKeys.forEach(value => {
          lineDataArr[value] = [];
        });

        for (let [index, item] of propData.entries()) {
          // x轴数据添加

          xAxis.push(item[this.params.xName]);
          dataKeys.forEach(value => {
            maxArr.push(item[value]);
            // 为每条line对应的数组添加数据
            lineDataArr[value].push(item[value]);
          });
        }
        let max = Math.max(...maxArr) + 100;
        let min = Math.min(...maxArr) - 100;
        if (min < 0) min = 0;

        dataKeys.forEach((value, index) => {
          let series = {
            name: map[value],
            type: "line",
            smooth: options.smooth === undefined ? false : options.smooth,
            smoothMonotone:
              options.smooth && options.smoothMonotone
                ? options.smoothMonotone
                : "none",
            symbolSize: 10,
            symbol: "circle",
            hoverAnimation: false,
            showSymbol: options.showSymbol === undefined ? false : options.showSymbol, //是否显示折线上的点
            itemStyle: {
              normal: {
                borderWidth: 2,
                borderColor: "#fff",
                color: this.params.colors[index].line
              },
              emphasis: {
                // borderWidth: 3,
                color: this.params.colors[index].line,
                borderColor: this.params.colors[index].start,
                borderWidth: 12
              }
            },
            lineStyle: {
              type: options.type ? (map[value] === options.dashKey ? "dotted" : "solid") : "solid",
              width: options.lineWidth || options.lineWidth === '0' ?options.lineWidth:3,
              color: this.params.colors[index].line
              //type:"dotted"
              // dashed
            },
            areaStyle: {
              normal: {
                opacity: options.areaShow === undefined ? true : options.areaShow,
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: this.params.colors[index].start
                    },
                    {
                      offset: 1,
                      color: this.params.colors[index].end
                    }
                  ],
                  false
                ),
                shadowColor: "rgba(0, 0, 0, 0.1)",
                shadowBlur: 10,
              }
            },
            data: lineDataArr[value]
          };
          seriesArr.push(series);
        });

        this.myChart = echarts.init(this.$refs.chartEl);
        this.option = {
          // 鼠标悬浮时弹框样式（所有图表需统一）
          tooltip: {
            trigger: "axis",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            padding: [10, 20],
            // axisPointer: {
            //   type: "line",
            //   lineStyle: {
            //     // color: options.Lcolor ? options.Lcolor : "#00d8d2",
            //     width: options.lineWidth ? options.lineWidth : 2
            //   }
            // },
            formatter: function (params) {
              var result = params[0].name + options.axisCategoryName + "<br/>";
              params.forEach(function (item, index) {
                let lastChar = item.seriesName.charAt(item.seriesName.length - 1);
                // 数据名称以比的加上百分号
                if (options.axisYName === '%') {
                  result += `${item.marker} ${item.seriesName} : ${
                    (item.value * 100).toFixed(2)
                    }%</br>`;
                } else {
                  // result += `${item.marker} ${item.seriesName} : ${item.value.toLocaleString()}</br>`;
                  result += `${item.marker} ${item.seriesName} : ${item.value}</br>`;
                }
              });
              return result;
            }
          },
          grid: {
            left: options.left ? options.left : "6%",
            right: options.right ? options.right : "6%",
            bottom: options.bottom ? options.bottom : "6%",
            top: options.top ? options.top : "6%",
            containLabel: true
          },
          legend: {
            icon: options.legendIcon ? options.legendIcon : "roundRect",
            // itemWidth: 10, //图例的大小
            show: options.legendShow === undefined ? true : options.legendShow,
            itemWidth: options.legendWidth ?  options.legendWidth : 20,
            itemHeight: options.legendHeight ? options.legendHeight : 5,
            color: "#fff",
            itemGap: options.legendItemgap ? options.legendItemgap : 20,
            right: options.legendRight ? options.legendRight : "5%",
            top: options.legendTop ? options.legendTop : "2%",
            textStyle: {
              color: "#fff", //图例颜色
              //fontSize: 14,
              fontSize: options.legendfontSize ? options.legendfontSize : 18
            },
            data: legendData
          },
          xAxis: [
            {
              type: "category",
              boundaryGap:
                options.boundaryGap === undefined ? false : options.boundaryGap,
              triggerEvent: true,
              interval: 1,
              data: xAxis,
              name: options.axisCategoryName ? options.axisCategoryName : "",
              nameLocation: options.nameLocation ? "start" : "end",
              nameTextStyle: {
                color: options.XaxisNameColor ? options.XaxisNameColor : "#fff",
                fontSize: options.axisLabelSize ? options.axisLabelSize : 20
              },
              axisLabel: {
                show: options.axisLabelShow ? true : false,
                margin: 10,
                fontSize: 16,
                color: this.$STANDARD.xAxisColor,
                formatter: params => {
                  return options.xAxisBr ? params.split("").join("\n") : params;
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  width: 2,
                  color: this.$STANDARD.xAxisLineColor
                }
              },
              axisTick: {
                alignWithLabel: true,
                //show: false,//x轴刻度是否显示
                show: options.axisTickShow ? true : false,
                inside: true //x轴刻度是否朝内
              },
              splitLine: {
                show: options.splitXLineShow ? true : false,
                lineStyle: {
                  color: "#1d2741" //网格线颜色（所有图表需要统一）
                }
              }
            }
          ],
          yAxis: [
            {
              type: "value",
              name: options.axisYName ? options.axisYName : "",
              nameLocation: options.nameLocation ? "start" : "end",
              nameTextStyle: {
                color: options.YaxisNameColor ? options.YaxisNameColor : "#ff517f",
                fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                fontWeight: 'bold',
                padding: [0, 45, 0, 0]
              },
              //scale: true, //y轴最小值是否为0
              scale: options.scales ? false : true,
              // max: this.params.areaShow === undefined ? null : max,
              max: this.params.axisMax === undefined ? null : this.params.axisMax,
              // min: this.params.areaShow === undefined ? null : min,
              min: this.params.axisMin === undefined ? null : this.params.axisMin,
              splitNumber: options.splitNumber ? options.splitNumber : 5,
              axisLabel: {
                show: options.YaxisaxisLabelShow ? false : true,
                margin: 10,
                fontSize: 14,
                color: options.axisLabelColor ? options.axisLabelColor : "#fff",
                // color: (value, index) => {
                //   let arr = [];
                //   let num = options.splitNumber ? options.splitNumber : 5;
                //   if (index < num) {
                //     for (let i = 0; i < num; i++) {
                //       arr.push(0)
                //     }
                //     let gradientC = gradientColor(arr, "#fdc48d", "#ff517f");
                //     return gradientC(index)
                //   } else {
                //     return "#ff517f"
                //   }
                // },
                formatter: (value) => {
                  if (options.axisYName === '%') {
                    return value * 100
                  } else {
                    return value
                  }
                }
              },
              axisLine: {
                show: options.YaxisLineShow ? true : false,
                width: 2,
                type: "dotted",
                color: this.$STANDARD.yAxisSplitLineColor
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: options.splitLineShow ? true : false,
                lineStyle: {
                  type: 'dashed',
                  color: "#2c344c" //网格线颜色（所有图表需要统一）
                }
              }
            }
          ],
          dataZoom: {
            show: options.dataZoomshow ? true : false,
            realtime: true, //拖动时实时更新
            // backgroundColor: "rgba(70,235,255,0.1)",
            fillerColor: "rgba(70,235,255,0.3)",
            borderColor: "rgba(70,235,255,0.6)",
            showDetail: false,
            handleIcon:
              "M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z",
            handleSize: "100%",
            bottom: 0,
            height: 20,
            start: options.dataZoomstart ? options.dataZoomstart : 0,
            end: options.dataZoomend ? options.dataZoomend : 100

            //itemWidth: options.legenditemWidth ? options.legenditemWidth : 10,
          },
          series: seriesArr
        };
        this.myChart.setOption(this.option);

        if (this.params.option.rollFlag) {
          this.chartMove();
          this.myChart.on("mouseover", params => {
            this.params.option.rollFlag = false;
            if (params.dataIndex === this.app.currentIndex) {
              // 序号相同不取消高亮
              return;
            } else {
              // 取消之前高亮的图形
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: this.app.currentIndex
              });
            }
          });
          this.myChart.on("mouseout", params => {
            this.params.option.rollFlag = true;
          });
        }

        //柱图点击事件
        this.myChart.on("click", params => {
          if (params.componentType === "series") {
            this.$emit("departmentClick", params.name);
          } else {
            this.$emit("departmentClick", params.value);
          }
        });
      },
      chartMove() {
        this.app = {};
        this.app.currentIndex = -1;
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.interval = setInterval(() => {
          let dataLen = this.params.data.length;
          if (this.params.option.rollFlag) {
            // 取消之前高亮的图形
            this.myChart.dispatchAction({
              type: "downplay",
              dataIndex: this.app.currentIndex
            });
            this.app.currentIndex = (this.app.currentIndex + 1) % dataLen;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              dataIndex: this.app.currentIndex
            });
            this.myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
          }
        }, 2000);
      }
    },
    computed: {
      // realData() {
      //   return this.params.data
      // }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      },
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  };
</script>

<style lang="stylus" scoped></style>
