<template>
	<div class="wrapper-bg">
		<div class="wrapper">
			<div class="content">
				<div class="w-top">
					<div class="t-left">
						<ctitle :title="'信访目的分布'"></ctitle>
						<div class="echarts">
							<RadiusPie :params="mdfbPieParams"></RadiusPie>
						</div>
					</div>
					<div class="t-right">
						<ctitle :title="'信访量目的分布走势'"></ctitle>
						<div class="echarts">
							<timeChange class="timeChange" @change="zsTimeClick"></timeChange>
              <LinesR :params="fbzsLineParams"></LinesR>
            </div>
					</div>
				</div>
				<div class="w-bottom">
					<ctitle :title="'信访量四级登记机构-信访目的分布'"></ctitle>
					<div class="inner">
						<div class="chart" v-for="(item, index) of xfmdfbArr" :key="index">
							<loopPie :params="item.params"></loopPie>
							<p class="label">{{ item.name }}</p>
						</div>
					</div>
				</div>
			</div>
    </div>
	</div>
</template>

<script>
import {get_http} from "@/api/index";
import ctitle from "@/components/title/index";
import loopPie from "@/components/pie/LoopPie1";
import LinesR from "@/components/line/LinesR";
import timeChange from "@/components/timeChange/index";
import RadiusPie from '@/components/pie/RadiusPie';
import CustomizedPie from '@/components/pie/CustomizedPie';
 export default {
    name: "xfmd",
    components: {
			ctitle,
			loopPie,
			LinesR,
			timeChange,
			RadiusPie,
			CustomizedPie
		},
    data() {
      return {
				// 信访目的分布
        mdfbPieParams: {
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            colors:['#7ae3ff','#ff965d','#a6f58c','#7766ca', '#ff5692'],   //环形颜色
            labelColor: '#fff',
            labelFontSize: 14, //21
            labelLineSmooth: 0,
						labelLineHeight: 0,
						minAngle: 50,
						radius: ["15%", "70%"],
						center: ["50%", "50%"],
          },
          data: [],
        },
				// 信访量目的分布走势
				fbzsLineParams: {
					data: [],
					colors: [
						// 紫
						{
							line: "rgba(14,149,210, 1)",
							start: "rgba(14,149,210, 0.5)",
							end: "rgba(14,149,210, 0.5)"
						},
						{
							line: "rgba(19,240,255, 1)",
							start: "rgba(19,240,255, 0.5)",
							end: "rgba(19,240,255, 0.5)"
						},
						{
							line: "rgba(78,237,153, 1)",
							start: "rgba(78,237,153, 0.5)",
							end: "rgba(78,237,153, 0.5)"
						},
						{
							line: "rgba(255,200,51, 1)",
							start: "rgba(255,200,51, 0.5)",
							end: "rgba(255,200,51, 0.5)"
						},
						{
							line: "rgba(243,152,71, 1)",
							start: "rgba(243,152,71, 0.5)",
							end: "rgba(243,152,71, 0.5)"
						}
					],
					option: {
						lineWidth: '0',
						legendWidth: 10,
						legendHeight: 10,
						YaxisLineShow: true,
						splitNumber: 4,
						splitLineShow: false,
						legendShow: true,
						legenditemWidth: 22,
						legenditemHeight: 4,
						axisLabelShow: true,
						axisTickShow: false,
						axisCategoryName: "月",
						// axisYName: "件次",
						axisLabelSize: 14,
						scales: true,
						axisLabelFontSize: 16,
						yaxisLabelFontSize: 14,
						Tofixed: false,
						smooth: false,
						areaShow: 1,
						smoothMonotone: "none",
						dataZoomshow: false,
						legendIcon: "rect",
						left: "5%",
						right: "10%",
						top: "15%",
						bottom: "10%",
						legendTop: "2%",
						legendRight: "10%"
					},
					canvasStyle: {
						width: "100%",
						height: "100%"
					},
					map: {
					},
					xName: "name"
				},
				//信访量四级登记机构-信访目的分布
        xfmdfbArr: [
          {name: "全国", params: {data: [], options: {}}},
          {name: "省级", params: {data: [], options: {}}},
          {name: "市级", params: {data: [], options: {}}},
          {name: "县级", params: {data: [], options: {}}}
				],
				xfmdfbPieParams: {
          options: {
						borderColor: '#050e27',
						borderWidth: 5,
						minAngle: 15,
            colors: this.$STANDARD.gradientColors,
						radius: ["50%", "70%"],
						center: ["50%", "53%"],
						// centerRadius: ["35%", "35%"],
						centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.4)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
						},
						rich: {
              bfontsize: "12%" //文字大小
            },
						labelSize: "18%",
            rollFlag: true
          }
        },
      };
		},
		mounted() {
			this.getAPI(this.year)
		},
    methods: {
			// 信访量目的分布走势
			getXflzsLineAPI(year, time) {
				let	_year = "";
				console.log('0000', time)
				if (time === "年度") {
					_year = "[now-4, now]";
					this.xfmdfbzs(_year, time)
				} else {
					_year = year;
					this.xfmdfbzs(_year, time)
				}
			},
			xfmdfbzs(year, time) {
				get_http("xftstjfxXfmdXfmdfbzs", {
					year: time === '年度'? year : null,
					gd_id4: time
				})
				.then(res => {
					this.fbzsLineParams.option.axisCategoryName =
						time === "年度" ? "年" : "月";
					this.fbzsLineParams.map = res.data.map;
					this.fbzsLineParams.data = res.data.data;
				})
				.catch(err => {
					console.log(err);
				});
			},
			getAPI(year) {
				// 1.信访目的分布
        get_http("xftstjfxXfmdXfmdfbPie", {
          year: year,
        })
        .then(res => {
          this.mdfbPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

				for (let item of this.xfmdfbArr.values()) {
          item.params.options = this.xfmdfbPieParams.options;
          get_http("xftstjfxXfmdXfmdfbloop", {
						year: year,
						gd_id4: item.name
          })
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
				}

				this.getXflzsLineAPI(year, '年度')
			},
			// 时间切换
			zsTimeClick(time) {

        this.getXflzsLineAPI(this.year, time);
      },
			fontSize(res) {
        let docEl = document.documentElement,
          clientWidth =
            window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
		},
		computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
.wrapper-bg
	background url("../../../assets/wrapper-bg2.png") no-repeat center
	background-size 100% 100%
	width 100%
	height 100%
	.wrapper
		position relative
		top 0.45rem
		height calc(100% - 45px)
		.content
			display flex
			flex-direction column
			height 100%
			padding .2rem .3rem
			box-sizing border-box
			.w-top
				flex 1
				display flex
				.t-left
					flex 2
					display flex
					flex-direction column
					.echarts
						flex 1
						// height 100%
				.t-right
					flex 3
					display flex
					flex-direction column
					.echarts
						flex 1
						position relative
						// height 100%
						.timeChange
							position absolute
							right 110px
							top -25px
			.w-bottom
				flex 1
				display flex
				flex-direction column
				.inner
					position relative
					z-index 1
					flex 1
					display flex
					align-items center
					justify-content space-around
					.label
						text-align center
						color #fff
						font-weight 700
						font-size 0.2rem
					.chart
						flex 1
						height 2.6rem
						margin-top -0.4rem
</style>
